import * as React from 'react'

import CodeBox from '../components/codebox/CodeBox'

const componentName = 'ButtonPage'

class ButtonPage extends React.Component {
  public static displayName = componentName

  public render() {
    return (
      <div>
        <section>
          <h1>Button </h1>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div className="container"></div>
          <CodeBox
            title=""
            description=""
          >
            {''}
          </CodeBox>
        </div>
        <div className="example">
          <div className="container"></div>
          <CodeBox
            title=""
            description=""
          >
            {''}
          </CodeBox>
        </div>
        <div className="example">
          <div className="container"></div>
          <CodeBox
            title=""
            description=""
          >
            {''}
          </CodeBox>
        </div>
        <div className="example">
          <div className="container"></div>
          <CodeBox
            title=""
            description=""
          >
            {''}
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>icon</td>
              <td className="des"> Icon  name </td>
              <td className="type">string</td>
              <td></td>
            </tr>
            <tr>
              <td>position</td>
              <td className="des"> Icon </td>
              <td className="type">'left' | 'right'</td>
              <td>'left'</td>
            </tr>
          </tbody>
        </table>
        <section>
          <h2>Method</h2>
        </section>
        <table className="method-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>focus()</td>
              <td className="des"></td>
              <td className="params"></td>
            </tr>
            <tr>
              <td>blur()</td>
              <td className="des"></td>
              <td className="params"></td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default ButtonPage



